      fg2.moveTo(((scale * (swordNow[0][0] / 4.5 * player[p].phys.face) + pos.x) * sc + soX), ((scale * (swordNow[0]
        [1] / -4.5) + pos.y) * -sc + soY));
      fg2.lineTo(((scale * (swordNow[1][0] / 4.5 * player[p].phys.face) + pos.x) * sc + soX), ((scale * (swordNow[1]
        [1] / -4.5) + pos.y) * -sc + soY));
